<template>
    <div class="container">
        <Title greeting-message="規章辦法"></Title>
        <div class="accordion" role="tablist">
            <div v-for="(item, index) in links" :key="index">
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-button block href="#" v-b-toggle="'accordion-' + index" variant="info">
                            查看
                        </b-button>
                        {{ item.name }}
                    </b-card-header>
                    <b-collapse :id="'accordion-' + index" accordion="my-accordion" role="tabpanel">
                        <b-card-body>
                            <b-card-text>
                                <a :href='"http://localhost/ntcust_lang_backend/public/api/file/"+item.url'>下載附件</a>{{  }}
                            </b-card-text>
                        </b-card-body>
                    </b-collapse>
                </b-card>
            </div>
        </div>
    </div>
    <div class="text-center">
        <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
    </div>
</template>

<script>
import Title from '../components/Title.vue';
import moment from 'moment';
import { LinkServer } from "../../providers/http-server.js";

export default {
    name: 'App',
    data() {
        return {
            links: []
        }
    },
    components: {
        Title
    },
    created()
    {
        this.moment = moment;
        LinkServer.read({group: true}).then(
            (response) => this.links = response.data.regulation
        )
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        }
    }
};
</script>

<style scoped>
@import '../../libs/ckeditor_plugin.css';
</style>